@tailwind base;
@tailwind utilities;
@tailwind components;
@tailwind variants;

:root {
	--focus-blue: deepskyblue;
	--border-gray: #999999;
	--content-gray: #999999;
	--text-default: #333333;
}

* {
	box-sizing: border-box;
	color: var(--text-default);
	transition-property: width, min-width, max-width, height, min-height, max-height, padding,
		padding-left, padding-right, padding-top, padding-bottom, margin, margin-left, margin-right,
		margin-top, margin-bottom, top, left, right, bottom, background-color, color, transform;
	transition-duration: 200ms;
	transition-timing-function: ease-in-out;
	transition-delay: 0ms;
}

*:focus,
*:active,
*:focus-within {
	outline: none;
}
